.element-container {
    background-color: #3e4652;
    border-bottom: 1px solid #333a43;
    border-top: 1px solid #495261;
    color: #fff;
    cursor: move;
    display: -ms-flexbox;
    display: flex;
    font-size: 15px;
}

.element-container:hover .element-icon {
    background-color: #0f83c1;
    color: #fff;
    text-decoration: none;
}

.element-container:hover .element-text {
    background-color: #2196F3;
    color: #fff;
    text-decoration: none;
}

.element-icon {
    background-color: #313841;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 28px;
    height: 54px;
    line-height: 54px;
    text-align: center;
    min-width: 54px;
}

.element-main-header {
    font-size: 18px;
    padding: 16px 48px 14px 18px;
}

.element-not-visible {
    background-color: #FFEB3B;
    border-radius: 5px;
    padding: 6px 9px 6px 30px;
    margin-top: 5px;
    width: 340px;
}

.element-properties {
    background-color: #3e4652;
    color: #fff;
    float: right;
    overflow-y: scroll;
    margin-right: -300px;
    min-width: 300px;
    -webkit-transition-property: margin;
    -o-transition-property: margin;
    transition-property: margin;
    -o-transition-duration: 600ms;
       transition-duration: 600ms;
    -webkit-transition-duration: 600ms;
    -webkit-transition-timing-function: ease;
         -o-transition-timing-function: ease;
            transition-timing-function: ease;
    width: 300px;
}

.element-properties input[type=text] {
    background-color: #535f70;
    border: 1px solid #596066;
    color: #fff;
}

.element-properties textarea {
    background-color: #535f70;
    border: 1px solid #596066;
    color: #fff;
}

.element-properties-show {
    margin-right: 0px;
}

.element-property {
    border-bottom: 1px solid #333a43;
    border-top: 1px solid #495261;
    padding: 15px;
}

.element-text {
    //border-bottom: 1px solid #212121;
    font-size: 14px;
    height: 54px;
    padding: 18px;
    text-transform: uppercase;
    //width: 246px;
    width: 300px;
}

.form-elements {
    background-color: #3e4652;
    color: #fff;
    float: left;
    /*margin-left: -300px;*/
    overflow-y: scroll;
    min-width: 300px;
    width: 300px;
    z-index: 2;
    -webkit-transition-property: margin;
    -o-transition-property: margin;
    transition-property: margin;
    -o-transition-duration: 600ms;
       transition-duration: 600ms;
    -webkit-transition-duration: 600ms;
    -webkit-transition-timing-function: ease;
         -o-transition-timing-function: ease;
            transition-timing-function: ease;
}


